import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.div`
  margin-bottom: 35px;
`;

const SubTitle = styled.h2`
  font-weight: normal;
  font-size: 32px;
`;

const Form = styled.form`
  max-width: 350px;
  margin: 35px auto;
`;

const InputDiv = styled.div`
  position: relative;
  margin-bottom: 10px;
  input {
    width: 100%;
    padding: 8px 8px 8px 28%;
    border: 1px solid #e9e8e7;
    outline: 0;
  }
`;

const InputLabel = styled.label`
  font-size: 13px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  padding: 9px;
  border-right: 1px solid #e9e8e7;
  width: 25%;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
`;

const RegisterButton = styled.button`
  background: #111111;
  color: white;
  width: 100%;
  padding: 8px 16px;
  border: 0;
`;

class LoginAndRegister extends Component {
  componentDidMount = () => {
    const list = [];
    const MaxLength = Math.floor(Math.random() * 5);
    for (var i = 0; i < 5; i++) {
      list.push(i);
    }
    list.sort(() => Math.random());
    console.log(list);
  };

  submit = () => {
    event.preventDefault();
  };

  handleChange = () => {
    console.log(event.target.name);
  };

  render() {
    return (
      <Div>
        <SubTitle>一、註冊行為</SubTitle>
        <Form onChange={this.handleChange}>
          <InputDiv>
            <InputLabel>帳號</InputLabel>
            <input type="text" name="account" />
          </InputDiv>
          <InputDiv>
            <InputLabel>密碼</InputLabel>
            <input type="password" name="password" />
          </InputDiv>
          <RegisterButton onClick={this.submit}>註冊</RegisterButton>
        </Form>
      </Div>
    );
  }
}

export default LoginAndRegister;
